#home {
    width: 100%;
    height: 100%;
}
html, body {
    font: normal normal normal 10pt / normal Arial, Helvetica, sans-serif;
    color: #9e9e9e;
    overflow-x: hidden; overflow-y: hidden;
}
#home .content {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    z-index: 1002;
}
#home .media {
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 1001;
    width: 100%;
    height: 100%;
}
.splash {
    overflow: hidden;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 0;
}
.splash img, .splash video {
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 0;
}
.splash-overlay-black {
    background-color: #000000;
}
#home .slides {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
}
#home .slides .slide {
    display: none;
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    z-index: 1;
}
#home .poster {
    display: none;
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    z-index: -1;
    -webkit-transform-origin: 0% 0%;
            transform-origin: 0% 0%;
}
#home .slides .slide .slide-content {
    position: absolute;
    top: 20%;
    left: 10%;
    width: 90%;
    height: 80%;
    pointer-events: none;
}
#home .slides .logos {
    position: relative;
    height: 140px;
    line-height: 140px;
    margin-bottom: 10vh;
    -webkit-transition: all 2s ease;
    transition: all 2s ease;
}
#home .slides .title {
    text-transform: uppercase;
    color: white;
    font-family: gothic,Arial,Helvetica,sans-serif;
    font-size: 4.5em;
    line-height: 0.9;
    letter-spacing: 0.1em;
}
#home .slides .title .line {
    overflow: hidden;
    position: relative;
    float: left;
    clear: both;
    -webkit-transition: all 1s ease;
    transition: all 1s ease;
}
#home .slides .title .line .sizer {
    opacity: 0;
}
#home .slides .title .line .display{
    position: absolute;
    top: 0px;
    left: 0px;
    -webkit-transform-origin: 0% 0%;
            transform-origin: 0% 0%;
}
#home .slides .logos img {
    height: 100%;
    display: inline-block;
    float: left;
   -webkit-transform-origin: 0% 0%;
           transform-origin: 0% 0%;
}
#home .slides .logos .plus {
    color: white;
    font-size: 3em;
    display: inline-block;
    float: left;
    margin: 0 .5em;
    -webkit-transform-origin: 0% 0%;
            transform-origin: 0% 0%;
}
#home .tabs .tab {
    width: 40px;
    height: 2px;
    margin-bottom: 30px;
    background-color: rgba(255, 255, 255, 0.3);
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    overflow: hidden;
    position: relative;
    display: inline-block;
}
#home .tabs .tab .slider {
    background-color: white;
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
#home #open-the-sound {
    position: absolute;
    top: 20%;
    right: -3px;
    border: rgba(255, 255, 255, 0.5) solid 3px;
    z-index: 1002;
    overflow: hidden;
    display: block;
    font-size: 16px;
    background: none;
    width: 240px;
    padding: 15px;
    text-align: left;
    font-family: gothic,Arial,sans-serif;
    color: #fff;
    letter-spacing: 2px;
    /* text-transform: uppercase; */
    cursor: pointer;
    -webkit-transition: color .2s;
    transition: color .2s;
}

/* #home #open-the-sound:before, */
#home #open-the-sound:after {
  content: '';
  position: absolute;
  top: 0;
  width: 100%;
  height:100%;
  background-color: white;
/*   transition: all 0.2s cubic-bezier(0.15, 0.75, 0.49, 0.93); */
  -webkit-transform:scaleY(0);
          transform:scaleY(0);
  z-index: -1;
}
#home #open-the-sound:after {
  -webkit-transform-origin: top left;
          transform-origin: top left;
/*   bottom: 0; */
  right: 0;
}
#home #open-the-sound:hover {
  color:#ff0000;
  -webkit-transition: color .2s ;
  transition: color .2s ;
}
#home #open-the-sound:hover:after {
  -webkit-animation: view-reel-hover 0.5s cubic-bezier(0.15, 0.75, 0.49, 0.93) both;
          animation: view-reel-hover 0.5s cubic-bezier(0.15, 0.75, 0.49, 0.93) both;
}
@-webkit-keyframes view-reel-hover {
0% {
    -webkit-transform:scaleY(1) scaleX(0);
            transform:scaleY(1) scaleX(0);
}
100% {
    -webkit-transform: scaleY(1) scaleX(1);
            transform: scaleY(1) scaleX(1);
}
}
@keyframes view-reel-hover {
0% {
    -webkit-transform:scaleY(1) scaleX(0);
            transform:scaleY(1) scaleX(0);
}
100% {
    -webkit-transform: scaleY(1) scaleX(1);
            transform: scaleY(1) scaleX(1);
}
}
